<template>
	<!-- 预算金额输入 -->
	<view class="form-group">
	  <label for="amount">金额</label>
	  <input 
	    type="number" 
	    v-model="bill.amount" 
	    placeholder="请输入金额"
	    @input="validateAmount"
	  />
	</view>
</template>

<script>
	export default {
		data() {
			return {
				budget:''
			}
		},
		methods: {
			validateAmount(e) {
			  const value = e.detail.value;
			  if (!/^\d*\.?\d*$/.test(value)) {
			    this.budget = value.replace(/[^0-9.]/g, '');
			  }
			}
		}
	}
</script>

<style lang="scss">
.form-group {
    margin-bottom: 20px;

    label {
      display: block;
      font-size: 14px;
      color: #666;
      margin-bottom: 5px;
    }
	}
</style>
